<template>
  <div>
    <router-view></router-view>
      <div class="main">
        <img src="./assets/logoko.png" />
        <h1 v-show="show" class="animated fadeInUp">1995-01-07</h1>
        <h1 v-show="show" class="animated fadeInUp">HeFei<i class="rhombus"></i>China</h1>
        <ul class="tab">
          <li class="bread-now" :to="{ path: '/' }">首页</li>
          <li>简介</li>
          <li>记录</li>
          <li>联系</li>
        </ul>
        <main-canvas :dotsNum="dotsNum" ></main-canvas>       
      </div>
  </div>
</template>

<script>
import mainCanvas from './components/mainCanvas'
export default {
  name: 'app',
  components: {
    mainCanvas
  },
  beforeCreate: function () {
    this.dotsNum = /Android|webOS|iPhone|iPod|BlackBerry/i.test(
      navigator.userAgent
    )
      ? 20
      : 60
  },
  mounted: function () {
    this.show = true
  },
  data () {
    return {
      roundColor: '#999',
      lineColor: '',
      isColor: true,
      show: false
    }
  }
}
</script>

<style lang="scss">
@import "./style/entry";

.main {
  text-align: center;
  box-sizing: border-box;
  padding-top: 16%;
}

img {
  width: 2.8rem;
  margin: auto;
}

h1 {
  text-align: right;
  width: 2rem;
  margin: auto;
  font-size: 0.2rem;
  line-height: 0.3rem;
}

.rhombus {
  display: inline-block;
  margin: 0 0.08rem 0.03rem 0.08rem;
  border-style: solid;
  border-width: 0.03rem;
  width: 0;
  height: 0;
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.animated {
  animation-duration: 2s;
  -webkit-animation-duration: 2s;
}

.tab {
  z-index: 20;
  position: fixed;
  left: .3rem;
  bottom: .4rem;
  display: flex;
  li{
    margin-right: .15rem;
    flex-direction: row;
    font-weight: 400;
    color: $color-black;
    font-size: .2rem;
    cursor: pointer;
    &:hover{
      color: $color-modal-blue;
    }
  }
}

</style>



